export default {
	data() {
		return {
			
			staffInfo: null,
			modalName: null,
			bookStatus: 0,
			curRoom: null,
			id: null,
			swiperList: [{
				id: 0,
				type: 'image',
				url: '../../static/girls/1.jpg'
			},{
				id: 0,
				type: 'image',
				url: '../../static/girls/1.jpg'
			},{
				id: 0,
				type: 'image',
				url: '../../static/girls/1.jpg'
			}],
			giftList: {},
			giftIndex: null,
			showGift: false,
			giftNum: 1,
			numSelect: false,
			giftId: null,
			orderId: null,
			
			
		};
	},
	computed: {
		style(){
			var CustomBar= this.CustomBar;
			var style = `top:${CustomBar}px; margin-top: 10px; width: 70px; height: 70px; border-radius: 100%; position: absolute; z-index: 999; right: 0`;
			return style
		}
	},
	onLoad(options) {
		this.id = options.id
		this.GetGiftList()
	},
	onShow() {
		this.GetStaffInfo()
	},
	methods:{
		SendRealGift(){
			var that = this
			
			var data = {
				count: this.giftNum,
				gid: this.giftId,
				orderId: '',
				staffId: this.id
			}
			uni.request({
				method: 'POST',
				header: {
					token: that.Token
				},
				data,
				url: that.Server + "gift/gift",
				success(res) {
					uni.hideLoading()
					if(res.data.code == 0){
						that.modalName = null
						uni.showModal({
							title: "感谢你对技师的打赏",
							confirmText: "继续打赏",
							success(res) {
								if(res.confirm){
									that.modalName = "bottomModal"
								}
							}
						})
					} else if (res.data.code == 101) {
						uni.showToast({
							title: '登录超时，将重新授权登录',
							duration: 500
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '../login/login'
							})
						}, 500)
					} else {
						that.modalName = null
						uni.showModal({
							content: res.data.msg
						})
					}
					uni.hideLoading()
				}
			})
			
		},
		ConfirmGift(){
			var that = this
			
			if(this.giftId != null){
				var data = {
					"count": this.giftNum,
					"gid": this.giftId
				}
				uni.request({
					method: 'POST',
					header: {
						token: that.Token
					},
					data,
					url: "https://www.qiafantech.com/club/wx/order/recharge",
					success(res) {
						
						if(res.data.code == 0){
							console.log(res);
							uni.showLoading({
								title: '支付中'
							})
							uni.requestPayment({
								appId: res.data.data.appId,
								nonceStr: res.data.data.nonceStr,
								package: res.data.data.package,
								paySign: res.data.data.paySign,
								signType: res.data.data.signType,
								timeStamp: res.data.data.timeStamp,
								success(res) {
									
									that.SendRealGift()
									
									console.log(res);
								}, fail (err){
									uni.hideLoading()
									that.modalName = null
									uni.showToast({
										icon: 'none',
										title: '支付失败'
									})
								}
							})
						} else if (res.data.code == 101) {
							uni.showToast({
								title: '登录超时，将重新授权登录',
								duration: 500
							})
							setTimeout(() => {
								uni.reLaunch({
									url: '../login/login'
								})
							}, 500)
						} else {
							uni.showModal({
								content: res.data.msg
							})
						}
						uni.hideLoading()
					}
				})
			} else {
				uni.showToast({
					icon: 'none',
					title: '请选择礼物'
				})
			}
			 
		},
		ChooseGift(id){
			console.log(id);
			this.giftId = id
		},
		closeSelect(){
			if(this.numSelect == true){
				this.numSelect = false
			}
		},
		chooseNum(num){
			this.numSelect = false
			this.giftNum = num
		},
		ShowNumSelection(){
			this.numSelect = !this.numSelect
		},
		GetGiftList(){
			var that = this
			uni.request({
				header: {
					token: that.Token
				},
				url: that.Server + 'g/list',
				success(res) {
					
					if(res.data.code == 0){
						var arr = []
						var index = 0
						for(var i in res.data.data){
							if(i != res.data.data.length - 1){
								if(arr.length == 3){
									that.giftList[index] = arr
									index++
									arr = []
									arr.push(res.data.data[i])
									that.giftList[index] = arr
								} else {
									arr.push(res.data.data[i])
									that.giftList[index] = arr
								}
							} else {
								if(arr.length == 3){
									that.giftList[index] = arr
									index++
									arr = []
									arr.push(res.data.data[i])
									that.giftList[index] = arr
								} else {
									arr.push(res.data.data[i])
									that.giftList[index] = arr
								}
							}
							
						}
						console.log(that.giftList);
					} else if (res.data.code == 101) {
						uni.showToast({
							title: '登录超时，将重新授权登录',
							duration: 500
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '../login/login'
							})
						}, 500)
					} else {
						uni.showModal({
							content: res.data.msg
						})
					}
					uni.hideLoading()
				}
			})
		},
		SendGift(){
			var that = this
			if(this.userServiceInfo == undefined){
				uni.showModal({
					title: '请授权微信登录',
					success(res) {
						if(res.confirm){
							uni.reLaunch({
								url: '../login/login'
							})
						}
					}
				})
			} else {
				if(this.giftList != null && this.giftList != undefined && this.giftList != {} && this.giftList != ''){
					that.modalName = "bottomModal"
				} else {
					uni.showToast({
						icon: 'none',
						title: '礼物未设置'
					})
				}
				
			}
		},
		hideModal(){
			this.modalName = null
		},
		showImg(index){
			uni.previewImage({
				urls: this.staffInfo.imgs,
				current: index,
				indicator: true,
				count:  this.staffInfo.imgs[index]
			})
		},
		GetStaffInfo(){
			uni.showLoading({
				title: '请稍等'
			})
			var that = this
			uni.request({
				header: {
					token: that.Token
				},
				url: that.Server + 'staff/info',
				data: {
					staffId: that.id
				},
				success(res) {
					
					if(res.data.code == 0){
						res.data.data.imgs = JSON.parse(res.data.data.imgs)
						that.staffInfo = res.data.data
					} else if (res.data.code == 101) {
						uni.showToast({
							title: '登录超时，将重新授权登录',
							duration: 500
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '../login/login'
							})
						}, 500)
					} else {
						uni.showModal({
							content: res.data.msg
						})
					}
					uni.hideLoading()
				}
			})
		},
		openModal(e){
			this.modalName = e.currentTarget.dataset.name
		},
		goBook(e){
			if(this.userServiceInfo == undefined){
				uni.showModal({
					title: '请授权微信登录',
					success(res) {
						if(res.confirm){
							uni.reLaunch({
								url: '../login/login'
							})
						}
					}
				})
			} else {
				uni.navigateTo({
					url: "../book/book?id=" + this.id
				})
			}
			
		},
		changeBookStatus(){
			if(this.bookStatus != 0){
				this.bookStatus = 0
			}
		},
		changeBookStatusB(){
			if(this.bookStatus != 1){
				this.bookStatus = 1
			}
			
		},
		changeRoom(e){
			// console.log(e);
			this.curRoom = e.currentTarget.dataset.index
		}
	}
}
